  z-index: 20;
}

.button:after {
  content: "";
  opacity: .2;
  z-index: 30;
  background: #fff;
  width: 50px;
  height: 155px;
  transition: all .55s cubic-bezier(.19, 1, .22, 1);
  animation: 1.5s infinite flash;
  position: absolute;
  top: -50px;
  left: -75px;
  transform: rotate(35deg);
}

.button:hover:after {
  transition: all .55s cubic-bezier(.19, 1, .22, 1);
  left: 120%;
}

@keyframes flash {
  0% {
    content: "";
    opacity: .2;
    z-index: 30;
    background: #fff;
    width: 50px;
    height: 155px;
    transition: all .55s cubic-bezier(.19, 1, .22, 1);
    position: absolute;
    top: -50px;
    left: -75px;
    transform: rotate(35deg);
  }

  100% {
    transition: all .55s cubic-bezier(.19, 1, .22, 1);
    left: 120%;
  }
}

.banner {
  background: linear-gradient(315deg, #65005e 3%, #3c84ce 38%, #30eee2 68%, #ff1919 98%) 0 0 / 400% 400% fixed;
  margin: auto;
